<template>
    <div class="p-formgroup-inline p-mb-2">
        <div class="p-field">
            <label>Embryo Assigned To</label>
            <p-dropdown
                v-model="form.primaryCycleId"
                :class="{
                    'p-invalid': v$.primaryCycleId.$invalid
                }"
                :options="primaryCycles"
                optionLabel="label"
                optionValue="cycleId"
                @change="onEmbyroAssingedToChageHandler"
            />
            <i
                v-if="v$.primaryCycleId.$invalid"
                class="pi pi-info-circle p-mt-2 p-ml-2"
                v-tooltip.right="v$.primaryCycleId.validator.$message"
                style="color: #f44336"
            ></i>
        </div>

        <div class="p-field">
            <label>Donor Patient Chart #</label>
            <!-- <p-dropdown
                v-model="form.otherPatientId"
                :options="[{ cycleId: 122 }]"
                optionLabel="cycleId"
                optionValue="cycleId"
            /> -->
            <p-auto-complete
                v-model="form.otherPatient"
                :suggestions="filteredPatient"
                field="label"
                placeholder="Type to search"
                @complete="patientFilter"
                @item-select="getOtherPrimaryCycles"
                optionLabel="label"
                optionValue="demographicNo"
            />
        </div>

        <div class="p-field">
            <label>Cycle ID</label>
            <p-dropdown
                v-model="form.otherPrimaryCycleId"
                :options="otherPrimaryCycles"
                optionLabel="cycleId"
                optionValue="cycleId"
                @change="onOtherCycleIdChangeHandler"
            />
        </div>
    </div>
</template>

<script>
import { useEbmryoInfo } from "@/composable/fertilization";
import { onMounted } from "vue-demi";
export default {
    setup() {
        const {
            store,
            form,
            v$,
            primaryCycles,
            filteredPatient,
            patientFilter,
            otherPrimaryCycles,
            getOtherPrimaryCycles,
            onOtherCycleIdChangeHandler,
            onEmbyroAssingedToChageHandler
        } = useEbmryoInfo();

        onMounted(() => {
            store.dispatch("fertilization/getPrimaryCycles");
        });

        return {
            form,
            v$,
            primaryCycles,
            filteredPatient,
            patientFilter,
            otherPrimaryCycles,
            getOtherPrimaryCycles,
            onOtherCycleIdChangeHandler,
            onEmbyroAssingedToChageHandler
        };
    }
};
</script>
